
// Прелоадер
document.addEventListener("DOMContentLoaded", function() {
  var preloader = document.querySelector(".preloader");
  var preloaderCircle = document.querySelector(".preloader__circle");

  if (!sessionStorage.getItem("preloaderShown")) {
    // Показываем прелоадер, если он еще не был показан пользователю
    preloader.classList.add("preloader__active");
    document.body.classList.add("lock");

    preloaderCircle.addEventListener("click", hidePreloader);
    
    // Убираем прелоадер спустя 5 секунд
    setTimeout(hidePreloader, 5000);
  }

  function hidePreloader() {
    // Убираем классы lock и preloader__active
    preloader.classList.remove("preloader__active");
    document.body.classList.remove("lock");

    // Добавляем анимацию для скрытия прелоадера с задержкой 0.5 секунды
    preloader.style.transition = "0.5s";
    preloader.style.opacity = "0";

    // Сохраняем информацию о показе прелоадера в sessionStorage
    sessionStorage.setItem("preloaderShown", "true");
  }
});


// Переход на предыдущую страницу
document.addEventListener("DOMContentLoaded", function(){
  const arrowLink = document.querySelector('.head__link');

  arrowLink.addEventListener('click', function(event) {
    event.preventDefault(); 

    const currentPage = window.location.href;

    if (currentPage === '/') {
      sessionStorage.removeItem('historyArray');  // Очищаем историю при достижении главной страницы
    } else {
      const historyArray = JSON.parse(sessionStorage.getItem('historyArray')) || [];
      historyArray.push(currentPage); // Добавляем текущую страницу в историю
      sessionStorage.setItem('historyArray', JSON.stringify(historyArray));
    }

    window.history.back();  // Возвращаем пользователя на предыдущую страницу
  });

  const currentPage = window.location.href;
  if (currentPage !== '/') {
    let historyArray = JSON.parse(sessionStorage.getItem('historyArray')) || []; 
    if(historyArray.length === 0 || historyArray[historyArray.length - 1] !== currentPage) {
      historyArray.push(currentPage);
      sessionStorage.setItem('historyArray', JSON.stringify(historyArray));
    }
  }
});


// скопировать ссылку
document.getElementById("copi").addEventListener("click", function() {
  var elementToCopy = document.getElementsByClassName("earn-play-to-earn__link")[0];
  var textToCopy = elementToCopy.innerText;
  
  navigator.clipboard.writeText(textToCopy)
    .then(function() {
      alert("Ссылка скопирована успешно");
    })
    .catch(function(error) {
      alert("Ссылка не была скопирована");
    });
});



// отправка формы в тг
document.addEventListener("DOMContentLoaded", function() {
  const form = document.getElementById('mailForm');

  form.addEventListener('submit', function(event) {
    event.preventDefault(); // Остановка отправки формы

    const name = document.getElementById('name').value;
    const phone = document.getElementById('phone').value;
    const message = document.getElementById('textarea').value;

    // Проверяем состояние чекбоксов
    const checkboxes = [
      { id: 'checkbox1', text: 'Анализ и оптимизация процессов' },
      { id: 'checkbox2', text: 'Маркетинговая стратегия и исследования' },
      { id: 'checkbox3', text: 'Выстраивание воронок продаж' },
      { id: 'checkbox4', text: 'Внедрение CRM и автоматизация' },
      { id: 'checkbox5', text: 'Обучение по продажам' },
      { id: 'checkbox6', text: 'Персональное наставничество' },
      { id: 'checkbox7', text: 'Разработка Telegram-ботов' },
      { id: 'checkbox8', text: 'Создание Telegram-приложений' },
      { id: 'checkbox9', text: 'Telegram + ИИ' },
    ];

    const selectedCheckboxes = checkboxes
      .filter(checkbox => document.getElementById(checkbox.id).checked)
      .map(checkbox => checkbox.text)
      .join(', ');

    // Формирование сообщения для Telegram
    const telegramMessage = `Новое сообщение:\nИмя: ${name}\nТелефон: ${phone}\nСообщение: ${message}\nВыбранные услуги: ${selectedCheckboxes || 'Нет'}`;

    // URL для отправки сообщения
    const token = '8181646645:AAEc7C2SJTd_Axxexos-_oFFWD9gpgVVXmk';
    const chatId = '697569114';
    const url = `https://api.telegram.org/bot${token}/sendMessage`;

    // Отправка сообщения в Telegram
    fetch(url, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        chat_id: chatId,
        text: telegramMessage,
        parse_mode: 'HTML' // Чтобы использовать HTML форматирование, если нужно
      })
    })
    .then(response => response.json())
    .then(data => {
      if (data.ok) {
        alert('Заявка успешно отправлена!');
        form.reset(); // Сброс формы после успешной отправки
      } else {
        alert('Ошибка отправки. Повторите попытку позже.');
      }
    })
    .catch(error => {
      console.error('Ошибка:', error);
      alert('Произошла ошибка. Попробуйте ещё раз.');
    });
  });
});